.list {
  padding: 0;
  margin: 10px 0 0 0;

  width: 100%;
  list-style: none;
}

.item {
  display: flex;
}

.item:not(:last-child) {
  margin-bottom: 10px;
}

.name {
  min-width: 160px;
}

.button {
  margin-left: auto;
  margin-right: 0;
  padding: 5px 10px;

  font-family: 'Roboto Mono', monospace;

  color: #fff;
  background-color: red;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  transform: scale(1.03);
}
